import React from 'react';
import './OrderSummary.scss';

import logoImg from '../assets/droplet-logo.png';
import logOutImg from '../assets/logout-icon.png';

class OrderSummaryComponent extends React.Component {
    constructor(props) {
        super(props);
    }

    onChange = () => {
    }

    render() {
        return (
            <div>
                <div className="header-section">
                    <div className="header-items">
                        <div className="header-left">
                            <img src={logoImg} className="droplet-logo"/>
                        </div>
                        <div className="header-right">
                            <label className="name-text clickable">Welcome back, Elson Ng</label>
                            <div className="slash-div"></div>
                            <label className="name-text clickable">Log out</label>

                            <img src={logOutImg} className="logout-img clickable"/>
                        </div>
                    </div>
                </div>
                <div className="progress">
                    <div className="finish-progress"></div>
                </div>

                <div className="container history-container">

                    <div className="row">
                        <div className="offset-1 col-10">
                            <div className="step-section">
                                <h4 className="step-text">Step 3 : Order Summary</h4>
                            </div>

                            <div className="pick-date-section">
                                <div className="row label-section">
                                    <div className="offset-1 col-4">
                                        <label className="title-text">Covered Dates</label>
                                    </div>
                                    <div className="col-5">
                                        <label className="title-text">Total Cost <span>(S$4/day)</span></label>
                                    </div>
                                </div>
                                <div className="row second-section">
                                    <div className="offset-1 col-4">
                                        <p className="date-time">Sat, 12 Mar <span>to</span> <br/>
                                            Sun, 13 Mar <span>2018</span></p>
                                        <div>
                                            <button className="btn btn-green btn-change-date">CHANGE DATE</button>
                                        </div>
                                    </div>
                                    <div className="col-5">
                                        <p className="money">S$8.00</p>
                                        <hr className="green-line"/>
                                        <p className="detail-text">
                                            <strong>2 Day Coverage</strong><br/>
                                            Cover 70% of Your Fare<br/>
                                            <span>Maximum Claim for 2 days:<br/>
                                                S$130 (up to S$65/day)</span>
                                        </p>
                                    </div>
                                </div>
                            </div>

                            <div className="table-section">
                                <table className="table order-table">
                                    <thead>
                                    <tr>
                                        <th>Order</th>
                                        <th>Details</th>
                                        <th></th>
                                        <th className="text-right">Total($)</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <tr>
                                        <td><strong>2 Day Coverages</strong></td>
                                        <td>12 Mar - 13 Mar 2018</td>
                                        <td></td>
                                        <td className="text-right">8.00</td>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td></td>
                                        <td className="text-right">Promo Code Applied(may31x):</td>
                                        <td className="text-right">-2.00</td>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td></td>
                                        <td className="text-right"><strong>Total:</strong></td>
                                        <td className="text-right total-number">6.00</td>
                                    </tr>
                                    </tbody>
                                </table>

                            </div>

                            <div className="promo-code-section">
                                <div className="form-group">
                                    <label htmlFor="code" className="promo-text">Promo Code, if any:</label>
                                    <input type="input" className="form-control promo-input" id="code"/>
                                </div>
                                <button className="btn btn-green btn-checkout">CHECKOUT NOW ➡</button>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


export default OrderSummaryComponent;



